/* animations */
.zent-zoom-appear, .zent-zoom-exit {
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.zent-zoom-appear.zent-zoom-appear-active {
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-name: zentZoomIn;
          animation-name: zentZoomIn;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}
.zent-zoom-exit.zent-zoom-exit-active {
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-name: zentZoomOut;
          animation-name: zentZoomOut;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
}
@-webkit-keyframes zentZoomIn {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes zentZoomIn {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes zentZoomOut {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
  }
}
@keyframes zentZoomOut {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  100% {
    opacity: 0;
    -webkit-transform: scale(0.2);
            transform: scale(0.2);
  }
}
/* base class for popups */
.zent-popup {
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  left: 0;
  outline: none;
  overflow-y: auto;
  position: relative;
  width: 100%;
  z-index: 2000;
  margin-top: 2px;
  background-color: #fff;
  max-height: 224px;
  border-radius: 3px;
  font-size: 12px;
}
